body {
  margin: 0;
}
.link {
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}
.link:hover {
  text-decoration: underline;
}
.countryInfoFlex {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(204, 204, 204, 0.4);
}
.countryInfoFlex:last-child {
  border-bottom: none;
}
p {
  margin: 0;
  padding: 6px 0;
}
.countryInfoDesc {
  text-align: right;
  max-width: 180px;
  line-height: 1.4;
}
